body{
  background-color: var(--primary-bg-color);

  @media screen and (max-width: 767px) {
    background-image: url(../images/sale/sale-bg-mob.jpg);
    background-repeat: no-repeat;
    background-size: cover;
  }

}



.container {
    width: 100%;
    margin: 0 auto;

    @include mobile {
        width: 480px;
        padding: 0 16px;
    }

    @include tablet {
        width: 768px;
        padding: 0 40px;
    }

    @include desktop {
        width: 1280px;
        padding: 0 40px;
    }
}
